@charset "UTF-8";
.content-list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: 2.25rem;
    margin-bottom: var(--measure-large);
    line-height: 1.25;
    line-height: var(--line-height-small);
}

.page--home .content-list,
.widget .content-list {
    margin-bottom: 0;
}

@media screen and (max-width: 500px) {
    .content-list li {
        width: 100%;
        display: inline-block;
    }
    .content-list li + li {
        margin-top: 1.25rem;
        margin-top: var(--measure-medium);
    }
}

@media screen and (min-width: 501px) {
    .content-list li {
        width: 49.01235%;
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
    }
}

@media screen and (min-width: 501px) {
    .content-list li:nth-child(n+3) {
        margin-top: 1.25rem;
        margin-top: var(--measure-medium);
    }
}

aside .content-list li {
    width: 100%;
}

aside .content-list li + li {
    margin-top: 0.625rem;
    margin-top: var(--measure-small);
}

.content-list article {
    flex: 1 0 100%;
    display: flex;
    flex-direction: column;
}

.content-list a {
    background-clip: padding-box;
    border-radius: 0.375rem;
    flex: 1 0 100%;
    display: block;
    padding: 1.25rem;
    padding: var(--measure-medium);
    overflow: hidden;
    text-decoration: none;
}

.content-list a:hover, .content-list a:focus, .content-list a:active {
    background-color: rgba(95, 138, 166, 0.1) !important;
    background-color: var(--color-raven--gamma) !important;
}

aside .content-list a {
    padding: 0.625rem;
    padding: var(--measure-small);
}

.content-list .title,
.content-list .lede,
.content-list .date {
    text-shadow: 0 1px 0 #f9f9f9;
    text-shadow: 0 var(--border-width-hairline) 0 var(--color-white);
}

.content-list .title {
    transition: text-decoration-color 0.2s ease 0s;
    will-change: text-decoration-color;
    width: auto;
    display: inline-block;
    margin-bottom: 0;
    text-decoration: underline;
    text-decoration-color: #dddddd;
    text-decoration-color: var(--color-alto);
    text-underline-position: under;
}

.content-list a:hover .title,
.content-list a:focus .title,
.content-list a:active .title {
    color: #5f8aa6;
    color: var(--color-raven);
    text-decoration-color: #ff0000;
    text-decoration-color: var(--color-canada);
}

.content-list .lede,
.content-list .date {
    color: #737373;
    color: var(--color-dove);
    margin-top: 0.625rem;
    margin-top: var(--measure-small);
    font-size: 0.875rem;
    font-size: var(--font-size-small);
    font-weight: 600;
    font-weight: var(--font-weight-bold);
}

.content-list .lede {
    margin-bottom: 0;
}

@media screen and (min-width: 801px) {
    .content-list .date {
        text-align: right;
    }
}

.content-list .date::before {
    content: "– ";
}

.content-list svg {
    transition: opacity 0.2s ease 0s, transform 0.2s ease 0s;
    will-change: opacity, transform;
    opacity: 0.05;
    opacity: var(--opacity-delta);
    stroke: #4f4f4f;
    stroke: var(--color-kaiser);
    width: 6.25em !important;
    height: 6.25em !important;
    position: absolute;
    top: 50%;
    right: 0.625rem;
    right: var(--measure-small);
    transform: translateY(-50%);
    z-index: 0;
    z-index: var(--z-index-root);
}

@media screen and (max-width: 500px) {
    .content-list svg {
        width: 4em !important;
        height: 4em !important;
    }
}

.content-list a:hover svg,
.content-list a:focus svg,
.content-list a:active svg {
    opacity: 0.1;
    opacity: var(--opacity-gamma);
    transform: translateX(-2.25rem) translateY(-50%) scale(1.2);
    transform: translateX(-var(--measure-large)) translateY(-50%) scale(1.2);
}
